            // this.imgSrc = '../assets/img/' + this.imgSrc;
            // console.log("=====this.xpoiledImgSrc=====")
            // console.log(this.xpoiledImg.xpoiledImgSrc)
            // console.log("=====this.cosmeticImgSrc=====")
            // console.log(this.cosmeticImg.cosmeticImgSrc)
        }, 1000)
    },
    computed: {
